//import {mapActions,mapState,mapMutations} from 'vuex';
import share from '@/components/share';
export default{
	components: {
		share
	},
	data() {
		return {
			productNumber:"",
			productSellNumber:"",
			productInfo:"",
			productName:"",
			product:{},
			productPrice:"",
			specClass: 'none',
			specSelected:[],
			productOriginalPrice:"",
			
			favorite: true,
			shareList: [],
			imgList: [
				{
					src: 'https://gd3.alicdn.com/imgextra/i3/0/O1CN01IiyFQI1UGShoFKt1O_!!0-item_pic.jpg_400x400.jpg'
				},
				{
					src: 'https://gd3.alicdn.com/imgextra/i3/TB1RPFPPFXXXXcNXpXXXXXXXXXX_!!0-item_pic.jpg_400x400.jpg'
				},
				{
					src: 'https://gd2.alicdn.com/imgextra/i2/38832490/O1CN01IYq7gu1UGShvbEFnd_!!38832490.jpg_400x400.jpg'
				}
			],
			desc: `
				<div style="width:100%">
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
				</div>
			`,
			specList: [
				{
					id: 1,
					name: '尺寸',
				},
				{	
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [
				{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = JSON.parse(options.id);
		// if(id){
		// 	this.$api.msg(`点击了${id}`);
		// }
		console.log(id)
		this.productNumber=id.productNumber;
		this.productSellNumber=id.productSellNumber;
		this.productName=id.productName;
		this.productInfo=id.productInfo;
		this.productPrice=id.productPrice;
		this.productOriginalPrice=id.productPrice/id.productInfo.discount
		this.product=id;
		//规格 默认选中第一条
		this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList');
	},
	methods:{
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
		},
		//加入购物车
		cat(){
		var cat={
					id: this.product.productId,
					image: this.product.productInfo.img[0],
					attr_val: this.product.productInfo.desc,
					stock: 15,
					title: this.product.productName,
					price: this.product.productPrice,
					number: 1
				}
			uni.request({
			    header:{
			    'Content-Type':'application/json;charset=UTF-8',
			    'token':'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOiJoencifQ.YqWDSsPa_2FLTb9Zh2-UWmjq4tpus5Yiz_MHVe8etaI'
			    },
			    method:'POST',
			    url:"http://49.233.188.52:8090"+"/frontend/cart/add",
			   // url:"http://localhost:8090"+"/frontend/cart/add",
			    data: {
					"cartInfo":JSON.stringify(cat)
				},
			    timeout:30000,
				dataType:"json",
			
			}).then(data => {//data为一个数组，数组第一项为错误信息，第二项为返回数据
						var [error, res]  = data;
						console.log("添加购物车响应结果",res)
						}
						)
		},
		//去购买
		buy(){
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},
		stopPrevent(){}
	},

}
